body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login__bg {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(/static/media/bg1.705ae073.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.login__bg .login__box--container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 320px;
  height: 440px;
  padding: 100px 40px 40px;
  margin-top: -290px;
  margin-left: -200px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background: -webkit-linear-gradient(220deg, rgba(53, 57, 74, 0), #000);
  background: linear-gradient(230deg, rgba(53, 57, 74, 0), #000);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="rgba(53, 57, 74, 0)", endColorstr="rgb(0, 0, 0)", GradientType=1);
  box-shadow: -15px 15px 15px rgba(0, 0, 0, 0.4);
  -webkit-transition: all 1s;
  transition: all 1s;
}
.login__bg .login__box--container .title {
  height: 60px;
  color: #d3d7f7;
  font-size: 18px;
  margin-bottom: 0;
}
.login__bg .login__box--container .login-form .button__group {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.menu__content--hrader {
  margin-bottom: 30px;
}
.btn-pad {
  padding: 5px 5px;
}


.blog__edit--wrap {
  height: 100%;
  overflow: auto;
}
.blog__edit--wrap .rdw-editor-main {
  background: rgba(100, 100, 100, 0.1);
  padding-left: 20px;
  padding-right: 20px;
}
.blog__edit--wrap .form-wrap {
  padding-top: 40px;
}
.article__content--header {
  margin-bottom: 30px;
}
.btn-pad {
  padding: 5px 5px;
}
.blog__markdown--editer {
  padding-top: 30px;
}
.coverImg img {
  width: 100px;
  height: 100px;
}

.link__content--header {
  margin-bottom: 30px;
}
.btn-pad {
  padding: 5px 5px;
}

html,body {
  height: 100%;
}
.header .sys__name {
  width: 120px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background: rgba(255,255,255, 0.2);  
  margin: 16px 28px 16px 0;
  float: left;
  padding: 0;
  color: #fff;
}
.header .sys__user {
  float: right;
  color: #fff;
}
.header .sys__user > div {
  position: relative;
}
.header .sys_logout {
  position: absolute;
  z-index: 9;
  top: 56px;
  width: 80px;
  padding-top: 0;
  padding-bottom: 0;
  cursor: pointer;
}
.header .ant-card-body {
  padding-top: 10px;
  padding-bottom: 0;
}
.header .sys_logout p {
  padding: 0;
}
.header .sys__user img{
  width: 30px;
  height: 30px;
  border-radius: 15px;
  margin-right: 10px;
}
.sider {
  overflow-x: hidden;
  overflow-y: auto;
  height: calc( 100vh - 64px );
}
.main__content {
  overflow-x: hidden;
  overflow-y: auto;
  height: calc( 100vh - 120px );
}

::-webkit-scrollbar{
  width: 10px;
  height: 10px;
  background-color: #f5f5f5;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
  height: 10px;
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #555;
}
::-moz-selection {
  background: #444444;
  color: #fff;
}
::selection {
  background: #444444;
  color: #fff;
}
